table {
  width: 100%;
}
td {
  vertical-align: top;
}
.container {
  width: 100%;
  height: 100%;
  min-width: 100%;
  min-height: 100%;
  position: relative;
}
.hero {
  background: url(../../images/ecosystem/globe_top.png) 0 40px no-repeat;
  background-size: cover;
}
.hero-bg {
  width: 100%;
  height: 100%;
  position: absolute;
  background: url(../../images/ecosystem/globe_top.png) no-repeat bottom center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-color: #FFFFFF;
  z-index: -3;
  transform: translate(0, -50%);
}
.title {
  font-family: "Noto Sans SC";
  font-weight: 400;
  color: #20242B;
  margin: 0 auto;
  text-align: center;
  padding: 60px 0;
  font-size: 3.125rem;
  line-height: 67px;
}
.title .sub {
  font-weight: 300;
  color: #1652F0;
  font-size: 1.25rem;
  line-height: 1rem;
  display: block;
  margin-bottom: 4px;
}
.tabs {
  display: flex;
  padding-top: 72px;
  overflow: hidden;
  justify-content: space-between;
  font-family: "Noto Sans SC";
  font-weight: 400;
  font-size: 1.125rem;
  color: #FFFFFF;
  flex-wrap: wrap;
  margin: 120px auto 0;
  width: 960px;
}
.tabs .tab {
  padding: 0.75rem 1.75rem 0.75rem 1.75rem;
}
.tabs .tab:hover {
  background-color: #1146D1;
  color: #EFCB5F;
  border-radius: 50px;
  cursor: pointer;
}
.selected {
  background-color: #1146D1;
  color: #EFCB5F;
  border-radius: 50px;
}
.tab-content {
  color: #F3F5F7;
  font-family: "Open Sans";
  font-size: 1rem;
  line-height: 25px;
  font-weight: 300;
  background-color: #164EE1;
  padding-top: 40px;
  box-sizing: border-box;
  min-height: 404px;
}
.tab-content .link {
  color: #EFCB5F;
}
.tab-content.progress .progress-list {
  overflow: hidden;
  width: 960px;
  margin: 0 auto;
}
.tab-content.progress .progress-list .progress-item {
  width: 61%;
  display: flex;
  margin-bottom: 60px;
}
.tab-content.progress .progress-list .progress-item .item-number {
  width: 60px;
  font-size: 40px;
  color: #EFCB5F;
  font-weight: 900;
  line-height: 1;
}
.tab-content.progress .progress-list .progress-item .item-info {
  flex: 1;
}
.tab-content.progress .progress-list .progress-item .item-info .info-title {
  padding-top: 6px;
  margin-bottom: 20px;
  font-size: 32px;
  color: #EFCB5F;
  line-height: 1;
}
.tab-content.progress .progress-list .progress-item:last-child {
  margin-bottom: 0;
}
.tab-content.progress .progress-list .progress-item-left {
  float: left;
}
.tab-content.progress .progress-list .progress-item-right {
  float: right;
  flex-direction: row-reverse;
  text-align: right;
}
.tab-content.progress .tip-list {
  width: 960px;
  margin: 0 auto;
  text-align: center;
}
.tab-content.progress .tip-list .tip-item {
  margin-top: 100px;
}
.tab-content.progress .tip-list .tip-item .tip-title {
  margin-bottom: 20px;
  font-size: 32px;
  color: #EFCB5F;
}
.tab-content.progress .tip-list .tip-item .tip-content {
  color: #fff;
}
.tab-content.progress .tip-list .tip-item:last-child {
  margin-bottom: 0;
}
.tab-content .answer p.text {
  margin-bottom: 16px;
}
.tab-content .answer p.text:last-child {
  margin-bottom: 0;
}
.header {
  text-align: center;
  padding: 0 0 80px 0;
}
.header p {
  max-width: 960px;
  margin: 0 auto;
  padding: 1rem 0 1rem 0;
}
.headline {
  font-weight: 900;
  font-family: "Noto Sans SC";
  font-size: 1.5rem;
  color: #FFFFFF;
  padding: 1.5rem 0 1rem 0;
}
.headline-sub {
  color: #EFCB5F;
}
.sect {
  padding: 20px 0;
  border: 1px solid #fff;
  border-radius: 20px;
  margin-bottom: 20px;
}
.sect:last-child {
  margin-bottom: 0;
}
.sect .number {
  font-size: 4.375rem;
  color: #EFCB5F;
  font-weight: 900;
  line-height: 1;
  vertical-align: text-bottom;
  padding: 1rem;
}
.sect .headline {
  color: #EFCB5F;
  text-transform: uppercase;
}
.sect .illustration {
  max-width: 250px;
  min-width: 60px;
  vertical-align: middle;
  display: none;
}
.sect .illustration img {
  display: block;
  width: 70%;
  margin: 0 auto;
}
.sect p {
  margin-top: 0.5rem;
}
.sect .item-num {
  color: #EFCB5F;
  font-weight: 900;
  font-size: 1.125rem;
}
.sect.right {
  text-align: right;
  -moz-text-align-last: right;
  text-align-last: right;
}
.sect.right table {
  width: 100%;
  margin-right: 0px;
  margin-left: auto;
}
.sect.right p {
  float: right;
}
.emphasized {
  display: block;
  font-size: 1.125rem;
  font-weight: 600;
  padding: 1rem 0 1rem 0;
  letter-spacing: 0.5px;
  line-height: 1.7rem;
}
.influence .flowchart {
  max-width: 918px;
  height: 100%;
  overflow: hidden;
  margin: 0 auto;
  display: block;
}
.flowchart img {
  display: block;
  width: 100%;
  margin: 0 auto;
}
.basics {
  margin: 0 6rem 0 6rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.description {
  width: 960px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.qna {
  flex-basis: 45%;
  padding: 1.5rem;
}
.des-block {
  flex: 1;
  padding: 30px;
  border: 1px solid #fff;
  border-radius: 12px;
}
.des-block:first-child {
  margin-right: 16px;
}
.des-block .title {
  font-size: 2.25rem;
  font-family: "Noto Sans SC";
  font-weight: 900;
  color: #EFCB5F;
  padding: 0;
  margin: 0;
  text-align: left;
  padding-bottom: 1.5rem;
  letter-spacing: 1px;
}
.question {
  font-size: 1.5rem;
  font-family: "Noto Sans SC";
  font-weight: 400;
  color: #EFCB5F;
  padding-bottom: 1rem;
}
.answer {
  max-width: 570px;
}
.illustration-globe {
  width: 100%;
  height: 300px;
  background: url(../../images/ecosystem/globe_bot.png) no-repeat bottom center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-color: #FFFFFF;
  z-index: -3;
}
.footer-cta {
  margin: 0 auto;
}
.footer-cta .download {
  justify-content: center;
}
.download {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-top: 3.5rem;
  width: 100%;
}
.download-btn:first-child {
  margin-right: 1rem;
}
.in-progress {
  margin: 0 auto;
  text-align: center;
  font-size: 1.5rem;
  color: #FFFFFF;
  opacity: 0.5;
  padding: 1em 0 1em 0;
}
@media only screen and (max-width: 414px) {
  .container {
    min-height: initial;
    min-width: initial;
    width: 100%;
    height: auto;
    padding-bottom: 160px;
  }
  .tabs {
    margin-top: 0;
    width: 100%;
  }
  .tabs .tab {
    flex: 1;
    padding: 10px 4px;
    font-size: 14px;
    text-align: center;
  }
  .tabs .tab:hover,
  .tabs .tab.selected {
    border-radius: 4px;
  }
  .hero {
    padding-top: 68px;
    background-size: contain;
    background-position: center bottom;
  }
  .hero .title {
    padding-left: 1rem;
    padding-right: 1rem;
  }
  .hero .title .strong {
    font-size: 36px;
  }
  .header p {
    padding: 1rem;
  }
  .tab-content {
    padding-top: 20px;
  }
  .tab-content.progress .header {
    padding-bottom: 40px;
  }
  .tab-content.progress .progress-list {
    width: 100%;
  }
  .tab-content.progress .progress-list .progress-item {
    width: 100%;
    margin-bottom: 20px;
    padding: 1rem;
  }
  .tab-content.progress .progress-list .progress-item .item-number {
    width: auto;
    margin-right: 8px;
    font-size: 32px;
    color: #EFCB5F;
    font-weight: 900;
    line-height: 1;
  }
  .tab-content.progress .progress-list .progress-item .item-info {
    flex: 1;
  }
  .tab-content.progress .progress-list .progress-item .item-info .info-title {
    margin-bottom: 20px;
    font-size: 24px;
  }
  .tab-content.progress .progress-list .progress-item-right {
    float: left;
    flex-direction: row;
    text-align: left;
  }
  .tab-content.progress .tip-list {
    width: 100%;
    margin: 0 auto 0;
    padding: 0 1rem;
  }
  .tab-content.progress .tip-list .tip-item {
    margin-top: 40px;
  }
  .tab-content.progress .tip-list .tip-item:first-child {
    margin-top: 0;
  }
  .tab-content.progress .tip-list .tip-item .tip-title {
    font-size: 24px;
  }
  .tab-content.progress .sect {
    margin: 0 auto;
    padding: 0;
    margin-bottom: 10px;
    padding-bottom: 20px;
  }
  .tab-content.progress .sect .number {
    font-size: 32px;
  }
  .tab-content.coin .basics {
    margin: 0 auto;
    padding: 0 1rem 0 1rem;
    margin-top: 2rem;
  }
  .tab-content.rewards {
    padding-top: 30px;
  }
  .tab-content.rewards .description {
    width: 100%;
    margin: 0 auto;
    padding: 0 1rem 0 1rem;
  }
  .tab-content.rewards .description .des-block {
    padding: 1rem;
  }
  .tab-content.rewards .description .des-block:first-child {
    margin-right: 0;
    margin-bottom: 20px;
  }
  .tab-content.rewards .description .des-block .title {
    font-size: 26px;
    padding-bottom: 0;
  }
  .sect.left td p {
    padding-right: 1rem;
  }
  .sect.right td p {
    padding-left: 1rem;
  }
  .qna {
    flex-basis: 100%;
  }
  .des-block {
    flex-basis: 100%;
  }
  .illustration-globe {
    height: 80px;
  }
  .footer.container {
    padding-bottom: 200px;
  }
}
