
@color-blue: #1652F0;
@color-red: #ff7f00;
@color-green: #4fbb92;
@color_1: #20242B;
@color_2: #1652F0;
@color_3: #FFFFFF;
@color_4: #EFCB5F;
@color_5: #F3F5F7;
@font_family_1: "Noto Sans SC";
@font_family_2: "Open Sans";
@background_color_1: #FFFFFF;
@background_color_2: #1146D1;
@background_color_3: #164EE1;

