table {
    width: 100%;
}

td {
    vertical-align: top;
}

.container {
    width: 100%;
    height: 100%;
    min-width: 100%;
    min-height: 100%;
    position: relative;
}

.hero {
    background: url(../../images/ecosystem/globe_top.png) 0 40px no-repeat;
    background-size: cover;
}

.hero-bg {
    width: 100%;
    height: 100%;
    position: absolute;
    background: url(../../images/ecosystem/globe_top.png) no-repeat bottom center;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    background-color: @background_color_1;
    z-index: -3;
    transform: translate(0, -50%);
}

.title {
    font-family: @font_family_1;
    font-weight: 400;
    color: @color_1;
    margin: 0 auto;
    text-align: center;
    padding: 60px 0;
    font-size: 3.125rem;
    line-height: 67px;
    .sub {
        font-weight: 300;
        color: @color_2;
        font-size: 1.25rem;
        line-height: 1rem;
        display: block;
        margin-bottom: 4px;
    }
}

.tabs {
    display: flex;
    padding-top: 72px;
    overflow: hidden;
    justify-content: space-between;
    font-family: @font_family_1;
    font-weight: 400;
    font-size: 1.125rem;
    color: @color_3;
    flex-wrap: wrap;
    margin: 120px auto 0;
    width: 960px;
    .tab {
        padding: 0.75rem 1.75rem 0.75rem 1.75rem;
        &:hover {
            background-color: @background_color_2;
            color: @color_4;
            border-radius: 50px;
            cursor: pointer;
        }
    }
}

.selected {
    background-color: @background_color_2;
    color: @color_4;
    border-radius: 50px;
}

.tab-content {
    color: @color_5;
    font-family: @font_family_2;
    font-size: 1rem;
    line-height: 25px;
    font-weight: 300;
    background-color: @background_color_3;
    padding-top: 40px;
    box-sizing: border-box;
    min-height: 404px;
    .link {
        color: @color_4;
    }
    &.progress {
        .progress-list {
            overflow: hidden;
            width: 960px;
            margin: 0 auto;

            .progress-item {
                width: 61%;
                display: flex;
                margin-bottom: 60px;
                .item-number {
                    width: 60px;
                    font-size: 40px;
                    color: @color_4;
                    font-weight: 900;
                    line-height: 1;
                }
                .item-info {
                    flex: 1;
                    .info-title {
                        padding-top: 6px;
                        margin-bottom: 20px;
                        font-size: 32px;
                        color: @color_4;
                        line-height: 1;
                    }
                }
                &:last-child {
                    margin-bottom: 0;
                }
                &-left {
                    float: left;
                }
                &-right {
                    float: right;
                    flex-direction: row-reverse;
                    text-align: right;
                }
            }

        }
        .tip-list {
            width: 960px;
            margin: 0 auto;
            text-align: center;
            .tip-item {
                margin-top: 100px;
                .tip-title {
                    margin-bottom: 20px;
                    font-size: 32px;
                    color: @color_4;
                }
                .tip-content {
                    color: #fff;
                }
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
    .answer {
        p.text {
            margin-bottom: 16px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

.header {
    text-align: center;
    padding: 0 0 80px 0;
    p {
        max-width: 960px;
        margin: 0 auto;
        padding: 1rem 0 1rem 0;
    }
}

.headline {
    font-weight: 900;
    font-family: @font_family_1;
    font-size: 1.5rem;
    color: @color_3;
    padding: 1.5rem 0 1rem 0;
}

.headline-sub {
    color: @color_4;
}

.sect {
    padding: 20px 0;
    border: 1px solid #fff;
    border-radius: 20px;
    margin-bottom: 20px;
    &:last-child {
        margin-bottom: 0;
    }
    .number {
        font-size: 4.375rem;
        color: @color_4;
        font-weight: 900;
        line-height: 1;
        vertical-align: text-bottom;
        padding: 1rem;
    }
    .headline {
        color: @color_4;
        text-transform: uppercase;
    }
    .illustration {
        max-width: 250px;
        min-width: 60px;
        vertical-align: middle;
        display: none;
        img {
            display: block;
            width: 70%;
            margin: 0 auto;
        }
    }
    p {
        //max-width: 700px;
        margin-top: 0.5rem;
    }
    .item-num {
        color: @color_4;
        font-weight: 900;
        font-size: 1.125rem;
    }
}

.sect.right {
    text-align: right;
    -moz-text-align-last: right;
    text-align-last: right;
    table {
        width: 100%;
        margin-right: 0px;
        margin-left: auto;
    }
    p {
        float: right;
    }
}

.emphasized {
    display: block;
    font-size: 1.125rem;
    font-weight: 600;
    padding: 1rem 0 1rem 0;
    letter-spacing: 0.5px;
    line-height: 1.7rem;
}

.influence {
    .flowchart {
        max-width: 918px;
        height: 100%;
        overflow: hidden;
        margin: 0 auto;
        display: block;
    }
}

.flowchart {
    img {
        display: block;
        width: 100%;
        margin: 0 auto;
    }
}

.basics {
    margin: 0 6rem 0 6rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.description {
    width: 960px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.qna {
    flex-basis: 45%;
    padding: 1.5rem;
}

.des-block {
    flex: 1;
    padding: 30px;
    border: 1px solid #fff;
    border-radius: 12px;
    &:first-child {
        margin-right: 16px;
    }
    .title {
        font-size: 2.25rem;
        font-family: @font_family_1;
        font-weight: 900;
        color: @color_4;
        padding: 0;
        margin: 0;
        text-align: left;
        padding-bottom: 1.5rem;
        letter-spacing: 1px;
    }
}

.question {
    font-size: 1.5rem;
    font-family: @font_family_1;
    font-weight: 400;
    color: @color_4;
    padding-bottom: 1rem;
}

.answer {
    max-width: 570px;
}

.illustration-globe {
    width: 100%;
    height: 300px;
    background: url(../../images/ecosystem/globe_bot.png) no-repeat bottom center;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    background-color: @background_color_1;
    z-index: -3;
}

.footer-cta {
    margin: 0 auto;
    .download {
        justify-content: center;
    }
}

.download {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-top: 3.5rem;
    width: 100%;
}

.download-btn {
    &:first-child {
        margin-right: 1rem;
    }
}

.in-progress {
    margin: 0 auto;
    text-align: center;
    font-size: 1.5rem;
    color: @color_3;
    opacity: 0.5;
    padding: 1em 0 1em 0;
}

@media only screen and (max-width: 414px) {
    .container {
        min-height: initial;
        min-width: initial;
        width: 100%;
        height: auto;
        padding-bottom: 160px;
    }

    .tabs {
        margin-top: 0;
        width: 100%;
        .tab {
            flex: 1;
            padding: 10px 4px;
            font-size: 14px;
            text-align: center;
            &:hover, &.selected {
                border-radius: 4px;
            }
        }
    }

    .hero {
        padding-top: 68px;
        background-size: contain;
        background-position: center bottom;
        .title {
            padding-left: 1rem;
            padding-right: 1rem;
            .strong {
                font-size: 36px;
            }
        }
    }

    .header {
        p {
            padding: 1rem;
        }
    }

    .tab-content {
        padding-top: 20px;
    }

    .tab-content.progress {
        .header {
            padding-bottom: 40px;
        }
        .progress-list {
            width: 100%;

            .progress-item {
                width: 100%;
                margin-bottom: 20px;
                padding: 1rem;
                .item-number {
                    width: auto;
                    margin-right: 8px;
                    font-size: 32px;
                    color: @color_4;
                    font-weight: 900;
                    line-height: 1;
                }
                .item-info {
                    flex: 1;
                    .info-title {
                        margin-bottom: 20px;
                        font-size: 24px;
                    }
                }
                &-left {

                }
                &-right {
                    float: left;
                    flex-direction: row;
                    text-align: left;
                }
            }

        }
        .tip-list {
            width: 100%;
            margin: 0 auto 0;
            padding: 0 1rem;
            .tip-item {
                margin-top: 40px;
                &:first-child {
                    margin-top: 0;
                }
                .tip-title {
                    font-size: 24px;
                }
                .tip-content {

                }
                .link {

                }
                &:last-child {

                }
            }
        }
        .sect {
            margin: 0 auto;
            padding: 0;
            margin-bottom: 10px;
            padding-bottom: 20px;
            .number {
                font-size: 32px;
            }
        }
    }

    .tab-content.coin {
        .basics {
            margin: 0 auto;
            padding: 0 1rem 0 1rem;
            margin-top: 2rem;
        }
    }

    .tab-content.rewards {
        padding-top: 30px;
        .description {
            width: 100%;
            margin: 0 auto;
            padding: 0 1rem 0 1rem;
            .des-block {
                padding: 1rem;
                &:first-child {
                    margin-right: 0;
                    margin-bottom: 20px;
                }
                .title {
                    font-size: 26px;
                    padding-bottom: 0;
                }
            }
        }
    }

    .sect.left {
        td {
            p {
                padding-right: 1rem;
            }
        }
    }

    .sect.right {
        td {
            p {
                padding-left: 1rem;
            }
        }
    }

    .qna {
        flex-basis: 100%;
    }

    .des-block {
        flex-basis: 100%;
    }

    .illustration-globe {
        height: 80px;
    }

    .footer.container {
        padding-bottom: 200px;
    }
}
